a {
  text-decoration: none;
  color: black;
}
body{
  width:100vw;
  margin-left: calc(-50vw + 50%);
}
#navibar{
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  font-size:20px;
  display: flex;
  justify-content: space-around;
  text-align: center;
  padding-top: 30px;
  margin-left: -6px;
  margin-top: -6px;
}
#navibar a{
  color: #401E20;
  padding-left: 5px;
}
#navibar #menu{
  margin-right:30px;
}
#navibar #rightbar{
  border-right: #401E20 1px solid;
  padding-right: 5px;
}
#navibar button{
  margin-left: 5px;
  background-color:transparent;
  border-radius: 5px;
  border: #F52334 1px solid;
  color: #F52334;
}
#banner{
  margin-top: calc(#navibar.height);
  display: flex;
  justify-content: center;
  margin-top: 20px;
  background-color: #CCEADE;
}
#banner img{
  height: 60%;
  width: 60%;
}